"use client";

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import api from "@/utils/api";

export default function Login() {
  const searchParams = use
 

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [msg, setMsg] = useState("");

  const lidaComLogin = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const res = await api.post("/login", { email, password });
      localStorage.setItem("token", res.data.token);
      setMsg("Login executado!");
    } catch {
      setMsg("Erro ao fazer login.");
    }
  };

  return (
    <div className="flex min-h-screen">
    
      <div className="hidden lg:block w-1/2 relative">
        <img
          src="/img-03.jpg" 
          alt="Login background"
          className="absolute inset-0 w-full h-full object-cover"
        />
      </div>

      <div className="w-full lg:w-1/2 flex items-center justify-center p-10">
        <div className="w-full max-w-md space-y-5">
          <h1 className="text-3xl font-bold text-center">Entrar</h1>

          <form onSubmit={lidaComLogin} className="flex flex-col gap-4">
            <div className="flex flex-col gap-1">
              <label>Email</label>
              <input
                type="email"
                placeholder="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="border p-3 rounded-lg"
              />
            </div>

            <div className="flex flex-col gap-1">
              <label>Senha</label>
              <input
                type="password"
                placeholder="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="border p-3 rounded-lg"
              />
            </div>

            <button className="bg-amber-500 hover:bg-amber-600 transition text-white rounded-lg p-3">
              Login
            </button>
          </form>

          {msg && (
            <p className="text-center mt-2 font-semibold">
              {msg}
            </p>
          )}
        </div>
      </div>
    </div>
  );
}
